let listaTareas = {
    0: [{ tarea: "Estudiar Examen", grupo: "calculo", tipoGrupo: "Universidad" }, { tarea: "Repasar Integrales Trigonometricas", grupo: "calculo", tipoGrupo: "Universidad" }, { tarea: "Sacar la basura", grupo: "Oficio", tipoGrupo: "Casa" }],
    1: [],
    2: [],
    3: [],
    4: [],
    5: [],
    6: [],
};

let listaGrupo = {
    "Universidad": ["calculo", "Programacion", "Algebra"],
    "Casa": ["Cuentas", "Oficio"],
}

for (let x = 0; x < Object.keys(listaTareas).length; x++) {
    mostrarTareas(x);
}

