function funcionCrearGrupo() {
    agregarGrupos.addEventListener('click', function () {
        mostrarGrupos.innerHTML += ` <form autocomplete="off">
            <input type="text" class="crearNuevoGrupo" id="crearNuevoGrupo" name="crearNuevoGrupo"
            placeholder="Nuevo Grupo" ">
        </form>`

        let crearNuevoGrupo = document.getElementById('crearNuevoGrupo')
        crearNuevoGrupo.addEventListener('keypress', (event) => {
            if (event.which == 13) {
                event.preventDefault();
                listaGrupo[`${crearNuevoGrupo.value}`] = [];
                funcionMostrarGrupos();
            }
        });
    })
}

function funcionMostrarGrupos(grupoEditar = '', subgrupo = '', subgrupoEditar = '', subgrupoEditarId = '') {
    let contadorColores = 0;
    let contadorIteracionesColores = 0;
    let listaColores = ["#dcc9fa", "#d3e5ef", "#e3e2e0"];
    mostrarGrupos.innerHTML = "";
    for (let grupo in listaGrupo) {

        if (grupoEditar == grupo) {
            mostrarGrupos.innerHTML += `
            <div class="infoGrupo">
                <form autocomplete = "off">
                    <input type="text" id="nombreNuevoGrupo" class="nombreNuevoGrupo" name="nombreNuevoGrupo"
                    autofocus onkeypress="funcionEditarNombreGrupo(event, '${grupo}')" >
                </form>
                <div class="imagenesGrupo">
                <img id=" eliminar${grupo}" src="/build/img/delete.svg" alt="eliminar Grupo">
                <img id="editar${grupo}" src="/build/img/edit.svg" alt="editar Grupo">
                <img id="crear${grupo}" src="/build/img/add.svg" alt="crear Grupo" onclick="funcionMostrarGrupos('','${grupo}')">
                </div>
            </div>`;
        }
        else {
            mostrarGrupos.innerHTML += ` <div class="infoGrupo">
            <p class="bold">${grupo}</p>
            <div class="imagenesGrupo">
                <img id="eliminar${grupo}" src="/build/img/delete.svg" alt="eliminar Grupo">
                <img id="editar${grupo}" src="/build/img/edit.svg" alt="editar Grupo">
                <img id="crear${grupo}" src="/build/img/add.svg" alt="crear Grupo" onclick="funcionMostrarGrupos('','${grupo}')">
            </div>
            </div>`;
        }
        for (let i = 0; i <= listaGrupo[grupo].length; i++) {


            if (subgrupo == grupo && i == 0) {

                mostrarGrupos.innerHTML += `<form autocomplete = "off">
                <input type="text" id="nombreNuevosubGrupo" class="nombreNuevosubGrupo" name="nombreNuevosubGrupo"
                autofocus onkeypress="funcionAñadirNombreSubgrupo(event, '${grupo}')" placeholder="Nombre Subgrupo">
                </form>`;
            }

            if (listaGrupo[grupo][i] != undefined) {

                if (subgrupoEditar != '' && parseInt(subgrupoEditarId) == i && subgrupoEditar == grupo) {

                    mostrarGrupos.innerHTML += `<div class="subgrupos">
                    <form autocomplete = "off">
                        <input type="text" id="nombreEditarSubgrupo" class="nombreEditarSubgrupo" name="nombreEditarSubgrupo"
                        autofocus onkeypress="funcionEditarNombreSubgrupo(event, '${grupo}', '${i}')" placeholder="${listaGrupo[grupo][i]}" >
                    </form>
                    <div class="imagenesGrupo">
                        <img id="eliminar${grupo}" src="/build/img/delete.svg" alt="eliminar Grupo" onclick="funcionEliminarSubgrupo('${grupo}','${i}')">
                        <img id="editar${grupo}" src="/build/img/edit.svg" alt="editar Grupo" onclick="funcionMostrarGrupos('','','${grupo}', '${i})'">
                    </div>
                    </div>`;
                } else {

                    mostrarGrupos.innerHTML += `<div class="subgrupos">
                    <p id="subgrupo${contadorIteracionesColores}"> ${listaGrupo[grupo][i]}</p>
                    <div class="imagenesGrupo">
                        <img id="eliminar${grupo}" src="/build/img/delete.svg" alt="eliminar Grupo" onclick="funcionEliminarSubgrupo('${grupo}','${i}')">
                        <img id="editar${grupo}" src="/build/img/edit.svg" alt="editar Grupo" onclick="funcionMostrarGrupos('','','${grupo}', '${i}')">
                    </div>
                    </div>`;
                }
            }
            let subgrupoi = document.getElementById(`subgrupo${contadorIteracionesColores}`);
            if (subgrupoi != null) {

                subgrupoi.style.backgroundColor = `${listaColores[contadorColores]}`
                contadorIteracionesColores++;
            }

        }
        contadorColores++;
        // mostrarGrupos.innerHTML += `<div class="linea"></div>`
    }


    mostrarGrupos.innerHTML += `<div class="agregarGrupos" id="agregarGrupos"><p>+ Nuevo</p></div>`
    let agregarGrupos = document.getElementById('agregarGrupos');
    agregarGrupos.addEventListener('click', function () {
        funcionCrearGrupo();
    })

    let eliminarGrupo = [];
    let nombreGrupo = [];
    for (const grupo in listaGrupo) {
        eliminarGrupo.push(document.getElementById(`eliminar${grupo}`));
        nombreGrupo.push(grupo);
    }
    for (let x = 0; x < eliminarGrupo.length; x++) {
        eliminarGrupo[x].addEventListener('click', function () {
            delete listaGrupo[`${nombreGrupo[x]}`];
            funcionMostrarGrupos();
        })
    }

    let editarGrupo = [];
    nombreGrupo = [];
    for (const grupo in listaGrupo) {
        editarGrupo.push(document.getElementById(`editar${grupo}`));
        nombreGrupo.push(grupo);
    }
    for (let x = 0; x < editarGrupo.length; x++) {
        editarGrupo[x].addEventListener('click', function () {
            funcionMostrarGrupos(nombreGrupo[x]);
        })
    }
}
function funcionEditarNombreGrupo(event, grupo) {
    let nombreNuevoGrupo = document.getElementById("nombreNuevoGrupo");
    if (event.which == 13) {
        event.preventDefault();
        listaGrupo[nombreNuevoGrupo.value] = listaGrupo[grupo];
        delete listaGrupo[grupo]
        funcionMostrarGrupos();
    }
}
function funcionAñadirNombreSubgrupo(event, grupo) {
    let nombreNuevosubGrupo = document.getElementById("nombreNuevosubGrupo");
    if (event.which == 13) {
        event.preventDefault();
        listaGrupo[grupo].push(nombreNuevosubGrupo.value)
        funcionMostrarGrupos();
    }
}
function funcionEliminarSubgrupo(grupo, i) {
    id = parseInt(i)
    delete listaGrupo[grupo].splice(id, 1);
    funcionMostrarGrupos();
    
}
function funcionEditarNombreSubgrupo(event, grupo, i) {
    let nombreEditarSubgrupo = document.getElementById("nombreEditarSubgrupo");
    if (event.which == 13) {
        event.preventDefault();
        listaGrupo[grupo].push(nombreEditarSubgrupo.value);
        listaGrupo[grupo].splice(i, 1)
        funcionMostrarGrupos();
        
    }
}
funcionLupaInput();
function funcionLupaInput() {
    lupaInput.addEventListener('keyup', (event) => {
        event.preventDefault();
        for (let i in listaTareas) {
            mostrarTareas(i, lupaInput.value);
        }
    })
}
funcionSort();
function funcionSort() {

    Sort.addEventListener('click', function () {
        for (let x in listaTareas) {
            listaTareas[x].sort(function (a, b) {

                if (a.tipoGrupo > b.tipoGrupo) {
                    return 1;
                }
                if (a.tipoGrupo < b.tipoGrupo) {
                    return -1;
                }
                return 0;
            });

            mostrarTareas(x);
        }
    })
}


function mostrarTareas(i, lupaInput = '') {
    mostrarTarea[i].innerHTML = '';
    for (let j = 0; j < listaTareas[i].length; j++) {
        if (listaTareas[i][j].tarea.includes(lupaInput)) {
            mostrarTarea[i].innerHTML += `
            <div class="headerTarea">
            <div class="tarea">
                <div class="tareaNombre">
                    <p>${listaTareas[i][j].tarea}</p>
                    <div class=tareaAjustes>
                        <img id="check" src="/build/img/check.svg" alt="check" onclick="(eliminarTarea('${i}','${listaTareas[i][j].tarea}'))">
                        <img id="edit" src="/build/img/edit.svg" alt="edit" onclick="(editarTarea('${i}', '${listaTareas[i][j].tarea}', '${listaTareas[i][j].grupo}'))">
                        <img id="trash" src="/build/img/delete.svg" alt="trash" onclick="(eliminarTarea('${i}','${listaTareas[i][j].tarea}'))">
                    </div>
                </div>
                <div class="tareaGrupo">
                    <p id="${listaTareas[i][j].tarea}">${listaTareas[i][j].grupo}</p>
                </div>
    
                </div>
            </div>`;
        }
        if (listaTareas[i][j].grupo == "") {
            let quitarPadding = document.getElementById(`${listaTareas[i][j].tarea}`)
            quitarPadding.style.padding = "0px"


        }

    }
    funcionContadorTareas();

}
for (let i = 0; i < agregarTareas.length; i++) {
    let vanAgregarTareas = false
    agregarTareas[i].addEventListener('click', function () {

        if (vanAgregarTareas == true) {
            formularioTarea[i].innerHTML = ''
            vanAgregarTareas = false
        }
        else {
            funcionFormularioTarea(i, '');
            vanAgregarTareas = true
        }

    });
}
function eliminarTarea(i, eliminar) {
    for (let j = 0; j < listaTareas[i].length; j++) {
        if (listaTareas[i][j].tarea == eliminar) {
            listaTareas[i].splice(j, 1);
        }
    }
    mostrarTareas(i);
    funcionContadorTareas();
}
function editarTarea(i, tarea) {
    for (let j = 0; j < listaTareas[i].length; j++) {
        if (listaTareas[i][j].tarea == tarea) {
            listaTareas[i].splice(j, 1);
        }
        // ENVIAR FORMULARIO PARA QUE SE PUEDA VISUALIZAR EN LA PAGINA
        mostrarTareas(i);
        funcionFormularioTarea(i, tarea);
        mostrarTareas(i);

    }
}

function funcionFormularioTarea(i, tarea) {

    formularioTarea[i].innerHTML = `
    <form autocomplete = "off">
        <input type="text" class="nombreTarea" id="nombreTarea" name="nombreTarea"
            placeholder="Tarea Nueva" value="${tarea}" autofocus>
    </form>
    <div class="select">
        <p id="select">> Grupo</p>
    </div>
    <div class="select subjectOculto" id="subject"></div>`
    let subject = document.getElementById('subject');
    let c = 0;
    for (let grupo in listaGrupo) {

        subject.innerHTML += `
            <div class="opcion">
                <p id="opcion">${grupo}</p>
                <div class="gruposOcultos" id="grupos">
                    
                </div>
            </div>`;

        let grupos = document.querySelectorAll('#grupos');
        for (let j = 0; j < listaGrupo[grupo].length; j++) {
            grupos[c].innerHTML += `<p id="grupo">${listaGrupo[grupo][j]}</p>`;
        }
        c++;
    }
    let select = document.getElementById('select');
    let opcion = document.querySelectorAll('#opcion');

    let grupo = document.querySelectorAll('#grupo');
    let vanSelect = false;
    select.addEventListener('click', function () {

        if (vanSelect == false) {
            subject.classList.add('subject');
            subject.classList.remove('subjectOculto');
            vanSelect = true;
        }
        else {
            subject.classList.remove('subject');
            subject.classList.add('subjectOculto');
            for (let x = 0; x < opcion.length; x++) {
                grupo[x].classList.remove('grupos');
                grupo[x].classList.add('gruposOcultos');
            }
            vanSelect = false;
        }


    });
    for (let x = 0; x < opcion.length; x++) {

        opcion[x].addEventListener('click', function () {
            grupos[x].classList.add('grupos');
            grupos[x].classList.remove('gruposOcultos');
            testigo = x;

            for (let k = 0; k < opcion.length; k++) {
                if (k != testigo) {
                    grupos[k].classList.remove('grupos');
                    grupos[k].classList.add('gruposOcultos');
                }
            }
        })
    }
    let subGrupoSeleccionado = "";
    let grupoSeleccionado = "";
    for (let x = 0; x < grupo.length; x++) {
        grupo[x].addEventListener('click', function () {
            select.innerHTML = `${grupo[x].textContent}`;
            select.classList.add('semi-bold')
            subGrupoSeleccionado = grupo[x].textContent;

            for (let k in listaGrupo) {
                if (listaGrupo[k].indexOf(grupo[x].textContent) != -1) {
                    grupoSeleccionado = listaGrupo[k];
                }
            }

            subject.classList.remove('subject');
            subject.classList.add('subjectOculto');
            for (let x = 0; x < opcion.length; x++) {
                grupos[x].classList.remove('grupos');
                grupos[x].classList.add('gruposOcultos');
            }
            vanSelect = false;
        })
    }


    let nombreTarea = document.querySelectorAll('#nombreTarea')

    nombreTarea[nombreTarea.length - 1].addEventListener('keypress', (event) => {

        if (event.which == 13) {
            event.preventDefault();
            listaTareas[i].push({ tarea: nombreTarea[nombreTarea.length - 1].value, grupo: subGrupoSeleccionado, tipoGrupo: grupoSeleccionado });
            formularioTarea[i].innerHTML = ' ';
            mostrarTareas(i);
            funcionSort();
        }

    });
}
